namespace $ { export class $mol_grider_demo extends $mol_grider {

	/// rowHeight 37
	rowHeight() {
		return 37
	}

	/// cellTitle# \
	cellTitle( key : any ) {
		return ""
	}

	/// cellContent# \
	cellContent( key : any ) {
		return ""
	}

	/// celler# $mol_grider_celler 
	/// 	title < cellTitle# 
	/// 	childs / < cellContent#
	@ $mol_mem_key()
	celler( key : any , next? : any , prev? : any ) {
		return new $mol_grider_celler().setup( obj => { 
			obj.title = () => this.cellTitle( key )
			obj.childs = () => [].concat( this.cellContent( key ) )
		} )
	}

} }

