namespace $ { export class $mol_grider extends $mol_viewer {

	/// tagName \table
	tagName() {
		return "table"
	}

	/// rowHeight 40
	rowHeight() {
		return 40
	}

	/// headerCellers /
	headerCellers() {
		return [] as any[]
	}

	/// header $mol_grider_rower 
	/// 	height < rowHeight 
	/// 	cellers < headerCellers
	@ $mol_mem()
	header( next? : any , prev? : any ) {
		return new $mol_grider_rower().setup( obj => { 
			obj.height = () => this.rowHeight()
			obj.cellers = () => this.headerCellers()
		} )
	}

	/// rowers /
	rowers() {
		return [] as any[]
	}

	/// childs / 
	/// 	< header 
	/// 	< rowers
	childs() {
		return [].concat( this.header() , this.rowers() )
	}

	/// gapTop 0
	gapTop() {
		return 0
	}

	/// gaperTop $mol_grider_gaper height < gapTop
	@ $mol_mem()
	gaperTop( next? : any , prev? : any ) {
		return new $mol_grider_gaper().setup( obj => { 
			obj.height = () => this.gapTop()
		} )
	}

	/// gapBottom 0
	gapBottom() {
		return 0
	}

	/// gaperBottom $mol_grider_gaper height < gapBottom
	@ $mol_mem()
	gaperBottom( next? : any , prev? : any ) {
		return new $mol_grider_gaper().setup( obj => { 
			obj.height = () => this.gapBottom()
		} )
	}

	/// cellers# /
	cellers( key : any ) {
		return [] as any[]
	}

	/// rower# $mol_grider_rower 
	/// 	height < rowHeight 
	/// 	cellers < cellers#
	@ $mol_mem_key()
	rower( key : any , next? : any , prev? : any ) {
		return new $mol_grider_rower().setup( obj => { 
			obj.height = () => this.rowHeight()
			obj.cellers = () => this.cellers( key )
		} )
	}

	/// cellerTitle# \
	cellerTitle( key : any ) {
		return ""
	}

	/// cellerHeader# $mol_floater childs / < cellerTitle#
	@ $mol_mem_key()
	cellerHeader( key : any , next? : any , prev? : any ) {
		return new $mol_floater().setup( obj => { 
			obj.childs = () => [].concat( this.cellerTitle( key ) )
		} )
	}

} }

namespace $ { export class $mol_grider_gaper extends $mol_viewer {

	/// height 0
	height() {
		return 0
	}

	/// heightStyle \0px
	heightStyle() {
		return "0px"
	}

	/// field * style.height < heightStyle
	field() {
		return $mol_merge_dict( super.field() , {
			"style.height" : ()=> <any> this.heightStyle() ,
		} )
	}

} }

