namespace $ { export class $mol_app_grider_branch extends $mol_checker_expander {

	/// level 0
	level() {
		return 0
	}

	/// levelStyle \0px
	levelStyle() {
		return "0px"
	}

	/// field * style.paddingLeft < levelStyle
	field() {
		return $mol_merge_dict( super.field() , {
			"style.paddingLeft" : ()=> <any> this.levelStyle() ,
		} )
	}

	/// expanded false
	@ $mol_mem()
	expanded( next? : any , prev? : any ) {
		return ( next !== void 0 ) ? next : false
	}

	/// checked > expanded
	checked( next? : any , prev? : any ) {
		return this.expanded( next , prev )
	}

	/// expandable false
	expandable() {
		return false
	}

	/// enabled < expandable
	enabled() {
		return this.expandable()
	}

	/// name \
	name() {
		return ""
	}

	/// label < name
	label() {
		return this.name()
	}

} }

