namespace $ { export class $mol_app_taxon extends $mol_pager {

	/// hierarhyField \Butxt
	hierarhyField() {
		return "Butxt"
	}

	/// rowers /
	rowers() {
		return [] as any[]
	}

	/// cellers# /
	cellers( key : any ) {
		return [] as any[]
	}

	/// grider $mol_grider 
	/// 	rowHeight 40 
	/// 	rowers < rowers 
	/// 	cellers# < cellers#
	@ $mol_mem()
	grider( next? : any , prev? : any ) {
		return new $mol_grider().setup( obj => { 
			obj.rowHeight = () => 40
			obj.rowers = () => this.rowers()
			obj.cellers = ( key : any ) => this.cellers( key )
		} )
	}

	/// body / < grider
	body() {
		return [].concat( this.grider() )
	}

	/// rowLevel# 0
	rowLevel( key : any ) {
		return 0
	}

	/// rowTitle# \
	rowTitle( key : any ) {
		return ""
	}

	/// rowExpanded# false
	@ $mol_mem_key()
	rowExpanded( key : any , next? : any , prev? : any ) {
		return ( next !== void 0 ) ? next : false
	}

	/// cellerBranch# $mol_app_grider_branch 
	/// 	title \Title
	/// 	level < rowLevel# 
	/// 	name < rowTitle# 
	/// 	expanded > rowExpanded#
	@ $mol_mem_key()
	cellerBranch( key : any , next? : any , prev? : any ) {
		return new $mol_app_grider_branch().setup( obj => { 
			obj.title = () => "Title"
			obj.level = () => this.rowLevel( key )
			obj.name = () => this.rowTitle( key )
			obj.expanded = ( next? , prev? ) => this.rowExpanded( key , next , prev )
		} )
	}

	/// cellerTitle# \
	cellerTitle( key : any ) {
		return ""
	}

	/// valueText# \
	valueText( key : any ) {
		return ""
	}

	/// cellerText# $mol_grider_celler 
	/// 	title < cellerTitle# 
	/// 	childs / < valueText#
	@ $mol_mem_key()
	cellerText( key : any , next? : any , prev? : any ) {
		return new $mol_grider_celler().setup( obj => { 
			obj.title = () => this.cellerTitle( key )
			obj.childs = () => [].concat( this.valueText( key ) )
		} )
	}

	/// valueNumber# 0
	valueNumber( key : any ) {
		return 0
	}

	/// cellerNumber# $mol_grider_number 
	/// 	title < cellerTitle# 
	/// 	childs / < valueNumber#
	@ $mol_mem_key()
	cellerNumber( key : any , next? : any , prev? : any ) {
		return new $mol_grider_number().setup( obj => { 
			obj.title = () => this.cellerTitle( key )
			obj.childs = () => [].concat( this.valueNumber( key ) )
		} )
	}

} }

