	@ $mol_mem()
	entered( next? : any , prev? : any ) {
		return ( next !== void 0 ) ? next : false
	}

	/// enter $mol_app_supplies_enter entered > entered
	@ $mol_mem()
	enter( next? : any , prev? : any ) {
		return new $mol_app_supplies_enter().setup( obj => { 
			obj.entered = ( next? , prev? ) => this.entered( next , prev )
		} )
	}

	/// supplies /
	supplies() {
		return [] as any[]
	}

