namespace $ { export class $mol_app_hello extends $mol_viewer {

	/// namerHint @ \Name
	namerHint() {
		return this.localizedText( "namerHint" )
	}

	/// name \
	@ $mol_mem()
	name( next? : any , prev? : any ) {
		return ( next !== void 0 ) ? next : ""
	}

	/// namer $mol_stringer 
	/// 	hint < namerHint 
	/// 	value > name
	@ $mol_mem()
	namer( next? : any , prev? : any ) {
		return new $mol_stringer().setup( obj => { 
			obj.hint = () => this.namerHint()
			obj.value = ( next? , prev? ) => this.name( next , prev )
		} )
	}

	/// message \
	message() {
		return ""
	}

	/// childs / 
	/// 	< namer 
	/// 	< message
	childs() {
		return [].concat( this.namer() , this.message() )
	}

} }

